@tailwind base;
@tailwind components;
@tailwind utilities;

body {
    margin: 0;
    padding: 0;
  }

  
  .min-h-screen {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  .flex-1 {
    flex: 1 0 auto;
    overflow-y: auto;
    height: calc(100vh - 128px); /* Высота экрана минус высота header и bottom navigation */
  }
  
  .fixed {
    position: fixed;
  }
  
  .bottom-0 {
    bottom: 0;
  }
  
  